@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
body {
  margin: 0;
}
* {
  font-family: 'Courier Prime', monospace;
}
.poster-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  justify-items: center;
  grid-gap: 15px;
  gap: 15px;
  margin: 2em 1em;
}

.poster {
  height: 300px;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  box-shadow: 8px -5px 10px rgb(0, 0, 0);
  border: 1px solid white;
}

.poster:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.poster img {
  height: 100%;
}
header {
  display: -webkit-flex;
  display: flex;
  padding: 0.5em 2em;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: black;
  color: white;
  -webkit-align-items: center;
          align-items: center;
}

.title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.title img {
  -webkit-filter: invert(100%);
          filter: invert(100%);
  height: 4em;
}

.title h1 {
  margin-left: 0.5em;
}

.scoreboard p {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  font-size: 1.1em;
}

.score-label {
  margin-right: 1em;
}
footer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 1.5em 0;
  background-color: black;
  color: white;
}

footer p {
  margin: 0;
}

footer div {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.5em;
}

footer a {
  margin: 0 0.5em;
}
